.business-main-container .margin-padding {
  max-width: 1110px;
  padding: 0 40px;
  margin: auto; }
  @media screen and (max-width: 991px) {
    .business-main-container .margin-padding {
      padding: 0 15px; } }

.business-main-container .carousel-container {
  margin-top: 80px;
  display: flex;
  flex-direction: column;
  align-items: center; }
  @media screen and (max-width: 991px) {
    .business-main-container .carousel-container {
      margin-bottom: 20px; } }
  .business-main-container .carousel-container .insurer-carousel-label {
    color: #2C3652;
    font-size: 14px;
    font-weight: bold;
    margin-bottom: -20px; }
  .business-main-container .carousel-container .insurer-logo-carousel {
    max-width: 650px;
    width: 100%;
    display: flex;
    justify-content: center;
    transform: translateY(-30px); }
    .business-main-container .carousel-container .insurer-logo-carousel .logo {
      height: 50px;
      display: flex; }
      .business-main-container .carousel-container .insurer-logo-carousel .logo img {
        margin: auto; }

.business-main-container .text-link {
  margin-left: 3px;
  font-size: 14px;
  font-weight: bold;
  color: #00D7AF; }
  .business-main-container .text-link:hover {
    text-decoration: underline; }

.business-main-container .slider-container .slick-slider {
  width: calc(100vw);
  transform: translateX(-15px);
  overflow: hidden;
  padding-bottom: 28px; }
  .business-main-container .slider-container .slick-slider .slick-dots {
    position: relative;
    transform: translateY(-28px);
    z-index: 1; }

.business-main-container .main-container .content {
  max-width: 600px;
  margin: auto; }

.business-main-container .main-container h1 {
  font-weight: 800;
  color: #6480EB; }

.business-main-container .main-container h2 {
  font-size: 24px;
  font-weight: 600;
  color: #2C3652;
  margin-bottom: 20px; }
  @media screen and (max-width: 767px) {
    .business-main-container .main-container h2 {
      font-size: 18px;
      margin-bottom: 30px; } }

.business-main-container .main-container h6 {
  font-size: 14px;
  font-weight: 600; }

.business-main-container .main-container h6.footnote {
  margin-top: 15px; }

.business-main-container .main-container .response-container {
  justify-content: center;
  background-color: #F9FBFF;
  padding-top: 5px;
  padding-bottom: 5px;
  border-radius: 15px; }
  .business-main-container .main-container .response-container .response-label {
    align-self: center;
    padding-left: 5px;
    margin-bottom: 0px; }

.business-main-container .main-container .main-img {
  max-width: 650px;
  margin: auto; }
  .business-main-container .main-container .main-img img {
    width: 100%;
    height: auto; }

.business-main-container .main-container .learn-more-button {
  margin-top: 10px; }
  @media screen and (max-width: 991px) {
    .business-main-container .main-container .learn-more-button {
      display: flex;
      justify-content: center; } }

@media screen and (max-width: 767px) {
  .business-main-container .main-container h6.footnote {
    margin-top: 20px; }
  .business-main-container .main-container img {
    margin: 0; } }

@media screen and (max-width: 1023px) {
  .business-main-container .main-container h1, .business-main-container .main-container h6 {
    text-align: center; } }
